import styles from './Slide.module.css';
import { useCallback, useEffect, useState } from 'react';
import MovieSlide from './MovieSlide';
import {
  IoIosArrowDropleftCircle,
  IoIosArrowDroprightCircle,
} from 'react-icons/io';
import Load from '../components/Load';

// Home
const Slide = ({ movieApi }) => {
  const [loading, setLoading] = useState(true);
  const [movies, setMovies] = useState([]);
  const [trans, setTrans] = useState(0);

  const onClickLeft = () => {
    if (trans >= 0) {
      // trans 0 
      return;
    }
    setTrans((prevTrans) => prevTrans + 460);
    //    width 460px
  };

  const onClickRight = () => {
    if (trans <= -1380) {
      // 460 * 3 = 1380
      return;
    }
    setTrans((prevTrans) => prevTrans - 460);
  };

  const getMovies = useCallback(async () => {
    setLoading(true);
    const json = await (await fetch(movieApi)).json();
    setMovies(json.data.movies);
    setLoading(false);
  }, [movieApi]);

  useEffect(() => {
    getMovies();
  }, [getMovies]);
  //   API 

  return (
    <div className={styles.container}>
      <div className={styles.slideShow}>
        {loading ? (
          <Load />
        ) : (
          <div
            className={styles.slides}
            style={{ transform: `translateX(${trans}px)` }}
          >
            {/* eslint-disable-next-line array-callback-return */}
            {movies.map((movie) => {
              if (movie.medium_cover_image != null) {
                return (
                  // MovieSlide is redering code with Slide!
                  <MovieSlide
                    key={movie.id}
                    id={movie.id}
                    coverImg={movie.medium_cover_image}
                    rating={movie.rating}
                    runtime={movie.runtime}
                    title={movie.title}
                  />
                );
              }
            })}
          </div>
        )}
      </div>

      {/* Left, Right  */}
      {loading ? null : (
        <div className={styles.buttons}>
          <button className={styles.left} onClick={onClickLeft}>
            <IoIosArrowDropleftCircle />
          </button>
          <button className={styles.right} onClick={onClickRight}>
            <IoIosArrowDroprightCircle />
          </button>
        </div>
      )}
    </div>
  );
};

export default Slide;
